.card-gallery {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    list-style: none;
    width: 100%;
}

/* Desktop */
@media screen and (width >= 1000px) {
    .card-gallery {
        flex-direction: row;
        flex-wrap: wrap;
    }
}
